.topbit {
    height: var(--topbit-height);
    position: relative;
    isolation: isolate;
    background-color: #ff9305;
    color: black;

}

#theNav {
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 9999;
}

#preNav {
    @media (min-width:992px) {
        border-bottom: 1px solid var(--col-brown-400);
    }
    background-color: white;
    .container-xl {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
        padding-block: 1rem;
        position: relative;
        isolation: isolate;
        z-index: 0;
    }

    .logo {
        background-image: url(../img/kingswood-wordmark.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        display: inline-block;
        aspect-ratio: 6 / 1;
        width: 200px;

        @media (min-width:545px) {
            aspect-ratio: 6 / 1;
            width: 200px;
        }
    }
    .contact {
        display: flex;
        justify-content: space-around; 
        gap: 1rem;
        align-items: center;
        font-size: var(--fs-300);
        font-weight: var(--fw-400);
        &__phone a {
            font-size: var(--fs-400);
            font-weight: var(--fw-700);
            color: var(--col-green-400);
        }
    }
    .button-container {
        width: 80px;
        justify-self: end;
        font-size: var(--fs-600);
        color: var(--col-secondary-400);
    }

    .navbar-toggle {
        width:20px;
        height:20px;
        background-color: transparent;
        border: none;
        background-size: contain;
        background-repeat: no-repeat;
    }
    .navbar-toggle {
        background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg id='Layer_2' data-name='Layer 2' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' viewBox='0 0 80 80'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: none; %7D .cls-2 %7B fill: %23b9a13c; %7D %3C/style%3E%3C/defs%3E%3Cg id='Layer_1-2' data-name='Layer 1'%3E%3Cpath class='cls-2' d='m68.29,68.29c-1.7,1.7-3.93,2.54-6.15,2.54s-4.44-.84-6.14-2.54l-16-16-16,16c-3.39,3.39-8.89,3.39-12.28,0-1.7-1.7-2.55-3.92-2.55-6.15s.85-4.45,2.55-6.14l16-15.99L11.72,24.01c-3.4-3.4-3.4-8.9,0-12.29,1.7-1.69,3.92-2.54,6.14-2.54s4.44.85,6.14,2.54l16,16,16-16c3.39-3.39,8.89-3.39,12.29,0,1.69,1.69,2.54,3.92,2.54,6.14s-.85,4.45-2.54,6.15l-16,16,16,15.99c3.39,3.39,3.39,8.89,0,12.29Z'/%3E%3Crect class='cls-1' width='80' height='80'/%3E%3C/g%3E%3C/svg%3E");
