#wrapper-navbar {

    background-color: var(--col-green-400);
    color: white;

    #navbar > div {
        flex-grow: 1;
    }

    .mega-menu {
        display: flex !important;
        justify-content: space-between;
    }
}

.mega-menu-item .image {
    width: 100%;
    object-fit: cover;
    object-position: center;
    aspect-ratio: 16 / 9;
}