.gform_fields {
    list-style-type: none;
    margin-right: -15px;
    margin-left: -15px;
    padding: 0;
}

.gfield {
    margin-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
}

.gform_wrapper.gravity-theme input[type=text],
.gform_wrapper.gravity-theme input[type=email],
.gform_wrapper.gravity-theme input[type=tel],
.gform_wrapper.gravity-theme textarea {
    @extend .form-control;
    // display: block;
    // width: 100%;
